import React from 'react';
import Shipment from './Shipment';
import PropTypes from 'prop-types'

class Order extends React.Component{

	static propTypes = {
		burgers: PropTypes.object,
		oreder: PropTypes.object,
		deleteFromOrder: PropTypes.func
	}

	renderOrder = key => {

		const burger = this.props.burgers[key];
		const count = this.props.order[key];
		const isAvailable = burger && burger.status === 'available'

		if(!burger) return null;

		if (!isAvailable){
			return <li className='unavailable' key={key}>
				Извините, {burger ? burger.name : 'бургер'} временно отсутствует
			</li>
		}

		return <li key={key}>
			<span>
			<span>{count}</span>
			шт. {burger.name}
			<span>{count * burger.price}Сум.</span>
			<button 
			onClick={() => this.props.deleteFromOrder(key)}
			className='cancelItem'>
				&times;
			</button>
			</span>

		</li>
	}


	render(){

		const orderIds = Object.keys(this.props.order);
		const total = orderIds.reduce((prevTotal, key)=>{
			const burger = this.props.burgers[key];
			const count = this.props.order[key];

			const isAvailable = burger && burger.status === 'available';
			if (isAvailable) {
				return prevTotal + burger.price * count;
			}
			return prevTotal;
		}, 0 )

		return(
			<div className='order-wrap'>
				<h2>Ваш заказ</h2>
				<ul  className = 'order'> {orderIds.map(this.renderOrder)}</ul>
		
				{total > 0 ? (
					<Shipment total= {total}/>
				) : (
					<div className = 'nothingSelected'>
						Выберите бургер и добавьте к заказу
					</div>
				)}

			</div>

		)
	}
}

export default Order;