import React from "react";
import PropTypes from 'prop-types'

class Shipment extends React.Component {

	static propTypes = {
		total: PropTypes.number
	}

	render() {
		const {total} = this.props;
		const shipping = total > 0 && total < 50000 ? 9900 : 4900;
		const shippingNeon = shipping === 4900 ? (
			<span className = 'font-effect-neon total_wrap-cheap'>
				{shipping} Сум.
			</span>
		) : (
			<span>{shipping} Сум.</span>
		)
		return (
			<div className='total'>
					<div className='total_wrap'>
						<div>
							<div>Доставка: {total > 0 ? shippingNeon : null}</div>
							<div className = ' total_wrap-free'>
								{total < 50000 ? `Закажите ещё на ${50000 - total} Сум. для доставки за 4900 Сум.`: null}
							</div>
						</div>
						<div calssName='total_wrap-final'>
							Итого: {total} Сум.
						</div>
					</div>
				</div>
		)
	}
}

export default Shipment;